@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
body{font-family:"Montserrat",sans-serif;margin:0;box-sizing:border-box}html{max-width:1440px;margin:0 auto}img{display:block;max-width:100%;height:auto}a{text-decoration:none}
.header{width:100%}.headerContainer{display:flex;flex-direction:column;justify-content:center;background-color:#373741;padding-top:100px;padding-bottom:50px;margin-top:30px;margin-bottom:80px}.name{text-align:center;color:#e1d8cf;font-size:45px}.profession{text-align:center;color:#fff;font-size:30px}
.App{display:flex;justify-content:center}.subHeader{padding:15px;color:#e1d8cf;background-color:#373741}.mainWrapper{width:60%}.small{color:#858585;display:block}
.sidebarContainer{width:40%;background-color:#e1d8cf;display:flex;flex-direction:column;align-items:center;padding:0 40px;height:100%}.myPhoto{margin:100px 0;width:400px;height:400px;object-fit:cover;border-radius:40%}
.aboutMeContainer{width:100%}.aboutMeContent{padding:20px}
.contactsContainer{width:100%}.contactLink{color:#09347a}.contactLink:hover{color:#626262}.cityName{margin-left:20px}
.mainContainer{padding:0 40px}
